.form {
  font-family: @upper-font;
}

.form__wrapper {
  @media (min-width: @desktop-width) {
    display: flex;
  }
}

.form__fildset {
  margin-bottom: 34px;
}

.form__fildset:nth-child(1),
.form__fildset:nth-child(2) {
  @media (min-width: @desktop-width) {
    width: 50%;
  }
}

.form__label {
  display: block;
}


.form__legend {
  margin: 0;
  margin-bottom: 36px;
  font-size: 24px;
  line-height: 26px;
  color: @color-headind;
}

.form__label,
.form__control::placeholder,
.form__textarea::placeholder {
  font-family: @upper-font;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: @color-main-text;
}

.form__text--color-radio {
  color: @color-card-title;
}

.form__text--color-checkbox {
  color: @color-headind;
}

.form__button {
  margin-bottom: 21px;
  font-size: 16px;
  line-height: 20px;
  padding: 7px 10px;
  border: none;
}

.form__note {
  display: block;
  font-family: @main-font;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  color: @color-headind;
}
